<template lang="html">
  <div class="container">
    <div class="error" v-if="!noError">
      <div class="jumbotron">
        <h1 v-if="error.noFind">
          <span class="glyphicon glyphicon-warning-sign" aria-hidden="true"></span>
          Sorry! 请检查你的地址是否输入有误？没找到该问卷。</h1>
        <h1 v-if="error.endTime">
          <span class="glyphicon glyphicon-warning-sign" aria-hidden="true"></span>
          Sorry! 该问卷已超过截止时间，现已关闭！</h1>
        <h1 v-if="error.noData">
          <span class="glyphicon glyphicon-warning-sign" aria-hidden="true"></span>
          Sorry! 数据加载异常，请检查您的网络！或者刷新一下网页试试！</h1>
        <p class="back-button"><a class="btn btn-primary btn-lg" v-link="{name: 'index'}" role="button">返回首页</a></p>
      </div>
    </div>
    <div class="content" v-if="noError">
      <div class="page-header">
        <h1>{{title}}<small class="endTime">结束时间：2016-9-3 23:59:59</small></h1>
      </div>
      <ul class="list-group">
        <li class="list-group-item" v-for="item of itemData" track-by="_uid" :class="{'notNull': !item.date.pickedVal.length && isNotNull}">
          <h4>{{ $index + 1 }} 、{{ item.date.itemVal }}
          <small v-if="item.type === 'radio'">（单选题）</small>
          <small v-if="item.type === 'checkbox'">（多选题）</small>
          <small v-if=" !item.date.pickedVal.length && isNotNull" class="notNull-error">* 该项必填</small>
        </h4>
          <div class="radio-item" v-if="item.type === 'radio'">
            <div class="option" v-for="option of item.date.radioValArry" track-by="$index">
              <input type="radio" :value="option" :name="item.date.itemVal" v-model="item.date.pickedVal">
              <label>{{ option }}</label>
            </div>
          </div>
          <div class="checkbox-item" v-if="item.type === 'checkbox'">
            <div class="option" v-for="option of item.date.radioValArry" track-by="$index">
              <input type="checkbox" :value="option" v-model="item.date.pickedVal">
              <label>{{ option }}</label>
            </div>
          </div>
          <div class="text-item" v-if="item.type === 'text'">
            <textarea v-model="item.date.pickedVal" placeholder="请填写内容！"></textarea>
          </div>
        </li>
      </ul>
      <div class="submit">
        <a class="btn btn-primary btn-lg active center-block" role="button" @click="checkNull()">提交问卷</a>
      </div>
      <!-- Modal- -->
      <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
        <div class="modal-dialog modal-sm" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
              <h4 class="modal-title" id="myModalLabel">提示</h4>
            </div>
            <div class="modal-body">您有一些必填项未填写, 请检查后再提交！</div>
            <div class="modal-footer">
              <button type="button" class="btn btn-default" data-dismiss="modal">确认</button>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      title: '标题',
      itemData: null,
      error: {
        endTime: false,
        noId: false,
        noData: false,
        noFind: true
      },
      isNotNull: false   // 检查必填项
    }
  },
  computed: {
    noError: function () {
      if (!this.error.endTime && !this.error.noId && !this.error.noData && !this.error.noFind) {
        return true
      } else {
        return false
      }
    }
  },
  ready () {
    let id = this.$route.query.id
    let data = JSON.parse(global.localStorage.getItem('issueDate'))
    if (id && data) {
      for (var i = 0; i < data.issueList.length; i++) {
        if (data.issueList[i].questionnaireID === id) {
          this.error.noFind = false
          if (new Date(data.issueList[i].endDate) > new Date()) {
            this.title = data.issueList[i].itemTitle
            this.itemData = data.issueList[i].itemList
          } else {
            this.error.endTime = true
          }
        }
      }
    } else {
      if (id) {
        this.error.noData = true
        this.error.noFind = false
      }
    }
  },
  attached () {},
  methods: {
    // 检查是否有未填写项
    checkNull: function () {
      this.isNotNull = false
      for (var i = 0; i < this.itemData.length; i++) {
        if (this.itemData[i].date.notNull && !this.itemData[i].date.pickedVal.length) {
          this.isNotNull = true
          $('#myModal').modal()
        }
      }
      if (!this.isNotNull) {
        this.setData()
        this.$router.go({name: 'succes'})
      }
    },
    // 存储数据
    setData: function () {
      let data = JSON.parse(global.localStorage.getItem('questionnaireData'))
      let id = this.$route.query.id
      let tempData = {}
      tempData.questionnaireId = id
      tempData.questionnaireTitle = this.title
      if (data) {
        let isFind = true
        for (var i = 0; i < data.length; i++) {
          if (data[i].questionnaireId === id) {
            tempData.data = this.itemData
            data[i].data.push(this.itemData)
            isFind = false
          }
        }
        if (isFind) {
          tempData.data = []
          tempData.data[0] = this.itemData
          data.push(tempData)
        }
      } else {
        tempData.data = []
        tempData.data[0] = this.itemData
        data = [tempData]
      }
      global.localStorage.setItem('questionnaireData', JSON.stringify(data))
    }
  },
  components: {}
}
</script>

<style lang="css" scoped>
.container{
  background-color: #fff;
  /*box-shadow: 0 0  6px 2px #999;*/
  padding: 10px;
  margin: 30px auto;
}
.error .glyphicon-warning-sign{
  color: #f00;
}
.error .back-button{
  margin-top: 10%;
  margin-left: 45%;
}
.page-header{
  text-align: center;
}
.page-header .endTime{
  display: block;
  text-align: right;
  margin-top: 10px;
}
.text-item textarea{
  margin-left: 30px;
  width: 80%;
  height: 100px;
}
.option{
  margin-left: 30px;
  display: inline-block;
}
.submit{
  width: 80%;
  margin: 50px 10% 20px 10%;
}
.notNull{
  outline: 1px solid #f00;
  margin-bottom: 1px;
}
.notNull-error{
  color: #f00;
}
</style>



